import React from "react"
import PropTypes from "prop-types"
import NavbarFixed from '../components/NavbarFixed';
import TagList from '../components/common/TagList';


// Components
import Link from 'gatsby-link'

const styles = {
  description: {
    color: '#576366',
  },
  timeToRead: {
    float: 'right'
  },
  tagsContainer: {
    flexDirection: 'row',
    display: 'flex',
    marginTop: 10,
    fontSize: 14,
  },
  tag: {
    marginRight: 10,
    backgroundColor: 'grey',
    padding: 5,
    paddingLeft: 8,
    paddingRight: 8,
    borderRadius: 6,
    fontWeight: 'bold',
  },
};

const Tags = (props) => {
  const { pathContext, data } = props;
  const { tag } = pathContext
  const { edges: posts, totalCount } = data.allMarkdownRemark
  const tagHeader = `${totalCount} post${
    totalCount === 1 ? "" : "s"
  } tagged with "${tag}"`

  return (
    <div className="blog-posts content">
      <NavbarFixed
        title={`TAGS/${tag}`}
        canGoBack
        onPress={() => {
          props.history.goBack();
        }}
      />

      <div style={{ marginLeft: 20 }}>
        {tagHeader}
        <br />
        <Link to="/tags">View all tags</Link>
      </div>

      {
      posts
        .filter(post => post.node.frontmatter.title.length > 0)
        .map(({ node: post }) => {
          return (
            <div className="row" key={post.id}>
              <div className="blog-post-preview col-md-8 offset-md-2">
                  <Link
                    style={{ color: 'black'}}
                    className="blog-post-preview-link"
                    to={post.frontmatter.path}>
                    <h4>
                      {post.frontmatter.title}
                    </h4>
                    <h6 style={styles.description}>{post.frontmatter.date}</h6>
                    <div>
                      <span style={styles.description}>{post.frontmatter.excerpt}</span>
                      <span style={styles.timeToRead}>{post.timeToRead} min read</span>
                    </div>
                    <TagList data={post.frontmatter.tags} />
                  </Link>
              </div>
            </div>
          );
        })
      }
    </div>
  )
}

Tags.propTypes = {
  pageContext: PropTypes.shape({
    tag: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      totalCount: PropTypes.number.isRequired,
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            frontmatter: PropTypes.shape({
              title: PropTypes.string.isRequired,
            }),
          }),
        }).isRequired
      ),
    }),
  }),
}

export default Tags

export const pageQuery = graphql`
  query TagsQuery($tag: String) {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          id
          timeToRead
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            excerpt
            tags
          }
        }
      }
    }
  }
`


