import React from 'react'
import Tag from './Tag'

const styles = {
  tagsContainer: {
    flexDirection: 'row',
    display: 'flex',
    marginTop: 10,
    fontSize: 14,
    flexWrap: 'wrap',
  },
};

export default function TagList({ data }) {
  return (
    <div style={styles.tagsContainer}>
      {
        (data || []).map((item) => {
          return (
            <Tag key={`tag-${item}`} item={item} />
          );
        })
      }
    </div>
  );
}



